import { TaxTypes } from '../types';

export const setTaxStatus = (displayPrice: number): TaxTypes => {
  let taxIncluded = false;
  let taxExcluded = false;

  switch (displayPrice) {
    case 1:
      taxExcluded = true;
      break;
    case 2:
      taxIncluded = true;
      break;
    case 3:
      taxIncluded = true;
      taxExcluded = true;
      break;
    default:
      taxIncluded = false;
      taxExcluded = false;
  }

  return { taxIncluded, taxExcluded };
};
